'use client';

import { useState, useEffect } from 'react';
import OnboardingWalkthrough from '@/components/OnboardingWalkthrough';

export default function Home() {
  const [showOnboarding, setShowOnboarding] = useState(false);

  useEffect(() => {
    // Only show onboarding for new users (after signup/account activation)
    const isNewUser = localStorage.getItem('isNewUser');
    const hasSeenOnboarding = localStorage.getItem('hasSeenOnboarding');
    if (isNewUser && !hasSeenOnboarding) {
      setTimeout(() => setShowOnboarding(true), 1000);
    }
  }, []);

  const handleCompleteOnboarding = () => {
    localStorage.setItem('hasSeenOnboarding', 'true');
    localStorage.removeItem('isNewUser');
    setShowOnboarding(false);
  };

  const handleSkipOnboarding = () => {
    localStorage.setItem('hasSeenOnboarding', 'true');
    localStorage.removeItem('isNewUser');
    setShowOnboarding(false);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 flex flex-col items-center justify-center">
      {showOnboarding && (
        <OnboardingWalkthrough
          onComplete={handleCompleteOnboarding}
          onSkip={handleSkipOnboarding}
        />
      )}
      {/* Only logo and CTA buttons for landing page */}
      <img
        src="/noxtitan-logo-new.png"
        alt="NoxTitan Logo"
        className="w-[400px] h-[120px] object-contain drop-shadow-2xl mb-10 mt-10"
        style={{ maxWidth: '100%', maxHeight: '120px', borderRadius: '1rem', background: 'none' }}
        onError={e => { e.currentTarget.src = '/noxtitan-logo.png'; }}
      />
      <h1 className="text-5xl md:text-6xl font-extrabold text-white mb-8 text-center drop-shadow-xl">
        Business Management.<br />
        <span className="bg-gradient-to-r from-purple-400 via-blue-400 to-pink-400 bg-clip-text text-transparent">Forged for Titans.</span>
      </h1>
      <div className="flex flex-row items-center gap-8 mt-2">
        <a href="/login" className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-3 px-8 rounded-full text-xl shadow-lg transition">Login</a>
        <a href="/contact" className="bg-gradient-to-r from-blue-500 to-purple-600 text-white font-bold py-3 px-8 rounded-full text-xl shadow-lg transition">Contact Me</a>
      </div>
    </div>
  );
}
